.node {
	display: inline-block;
	height: 32px;
	margin-left: -1px;
	margin-top: -1px;
	border: 1px solid #e2e8f0;
	width: 32px;
	/* touch-action: manipulation; */
}

.node.start {
	background-color: #e53e3e;
}

.node.finish {
	background-color: #38a169;
}

.node.wall {
	background-color: black;
}

.node.visited {
	background-color: #3182ce;
}

.node.path {
	background-color: #f6e05e;
}
